:root {
  --container-size: 768px;
  --bg: #000;
  --text: #fff;
  --primary-yellow: #f9f929;
}

*, :after, :before {
  box-sizing: border-box;
}

body {
  background: var(--bg);
  color: var(--text);
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif;
}

.container {
  max-width: var(--container-size);
  text-align: center;
  margin: 0 auto;
}

.container h1 {
  font-size: 36px;
  display: inline-block;
}

.container span {
  color: var(--primary-yellow);
  margin-left: 10px;
  font-size: 36px;
}
